* {
  text-decoration: none;
  margin: 2px;
  padding: 0;
  list-style: none;
}

.container {
  display: flex;
  height: 100vh;
}
.leftcontainer {
  position: fixed;
  flex: 30%;
  width: 250px;
  height: 100%;
  background: #042331;
}
.rightcontainer {
  flex: 70%;
}
header {
  text-align: center;
  color: white;
  font-size: 30px;
  padding: 20px;
  margin-bottom: 25px;
}

li a {
  display: block;
  height: 100%;
  width: 100%;
  line-height: 65px;
  font-size: 20px;
  color: white;
  padding-left: 40px;
  box-sizing: border-box;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  border-bottom: 1px solid black;
}

ul a:hover {
  padding-left: 20px;
  color: aqua;
}

@media screen and (max-width: 480px) {
  .leftcontainer {
    font-size: 50%;
  }
}
